import React from 'react';

function Banner({ list, links }) {
  return (
    <div class='table'>
      <div class='table-left'>
        <div class='photo'></div>
      </div>
      <div class='table-right'>
        <ul class='list-info'>
          <li class='list-header'>
            <h1>Phillip Stemmlar Schulze</h1>
          </li>
          {list.map((item) => (
            <li class='list-item'>{item}</li>
          ))}
          <li class='list-item icons icons-full'>
            {links.map((link) => {
              const [href, src, alt, colorClass] = link;
              return (
                <a href={href} target='_blank' rel='noopener noreferrer'>
                  <img alt={alt} src={src} className={'icon ' + (colorClass ? 'icon-black' : '')}></img>
                </a>
              );
            })}
          </li>
        </ul>
      </div>
    </div>
  );
}

export default Banner;
