import React from 'react';

function BulletListParagraph({ title, bodyList }) {
  return (
    <div class='paragraph'>
      <div class='paragraph-header'>
        <h2>{title}</h2>
      </div>
      <div class='paragraph-body'>
        <p>
          <ul class='bullet-list'>
            {bodyList.map((item, i) => (
              <li class='bullet-item'>{item}</li>
            ))}
          </ul>
        </p>
      </div>
    </div>
  );
}

export default BulletListParagraph;
