import React from 'react';

function WorkReferenceSection({ referenceList }) {
  if (referenceList)
    return (
      <ol class='work-section'>
        <li class='work-header'>References</li>
        {referenceList.map(([name, email, cell, comment]) => (
          <li class='work-ref'>
            <span class='ref-name'>
              {name}
              {comment && <span class='comment'>{comment}</span>}
            </span>
            {cell && <span class='ref-cell'>{cell}</span>}
            {email && <span class='ref-email'>{email}</span>}
          </li>
        ))}
      </ol>
    );
  else return '';
}

export default WorkReferenceSection;
