import React from 'react';

function SimpleParagraph({ title, children }) {
  return (
    <div class='paragraph'>
      <div class='paragraph-header'>
        <h2>{title}</h2>
      </div>
      <div class='paragraph-body'>
        <p>{children}</p>
      </div>
    </div>
  );
}

export default SimpleParagraph;
